import React from 'react'

const TableHeader = () => {
    return (
        <thead>
            <tr>
                <th>First name</th>
                <th>Name</th>
                <th>Job</th>
            </tr>
        </thead>   
    )
}

const TableBody = props => {
    const rows = props.character_Data.map((row, index) => {
    return (
        <tr key={index}>
            <td>{row.firstname}</td>
            <td>{row.name}</td>
            <td>{row.job}</td>
            <td><button onClick={() => props.removeCharacter(index)}>Delete</button></td>    
        </tr>        
    )
})
    return <tbody>{rows}</tbody> 
}

const Table = (props) => {
    const { character_Data, removeCharacter } = props;
  
    return (
      <table>
        <TableHeader />
        <TableBody character_Data={character_Data} removeCharacter={removeCharacter} />
      </table>
    );
}

export default Table